<template>
  <div id="novel-details">
    <div class="breadcrumb-box">
      <Breadcrumb>
        <Breadcrumb-item href="javascript:;" @click.native="routeTo('/home')">  </Breadcrumb-item>
        <Breadcrumb-item href="javascript:;" @click.native="routeTo('/novel')">  </Breadcrumb-item>
        <Breadcrumb-item href="javascript:;" @click.native="routeTo('/novel_chapter', novel.id)">  </Breadcrumb-item>
        <Breadcrumb-item>  </Breadcrumb-item>
      </Breadcrumb>
    </div>
    <Row>
      <Card class="table-box">
        <h2 class="chapter-title"><span>{{ novel.chinese_title }}({{ novel.title }})</span></h2>
        <hr>
        <br>
        <i-form ref="formItem" :model="formItem" :label-width="100" :rules="ruleFormItem">
          <Form-item label="()" prop="title">
            <i-input v-model="formItem.title" placeholder="..." style="width: 500px;"></i-input>
          </Form-item>
          <Form-item label="()" prop="chinese_title">
            <i-input v-model="formItem.chinese_title" placeholder="..." style="width: 500px;"></i-input>
          </Form-item>
          <Form-item label="">
            <Input-number :max="999" :min="0" v-model="formItem.order" :step="10"></Input-number>
            <span style="color: #ff5c33;">  tips:10,2,</span>
          </Form-item>
          <Form-item label="" prop="intro">
            <i-input v-model="formItem.intro" type="textarea" :autosize="{ minRows: 6, maxRows: 8 }" placeholder="..."></i-input>
          </Form-item>
          <Form-item label="" prop="content" class="editor-box">
            <quill-editor ref="myTextEditor"
                          v-model="formItem.content"
                          :config="editorOption"
                          style="height: 260px;">
            </quill-editor>
          </Form-item>
          <Form-item>
            <i-button type="primary" @click.native="handleSubmit('formItem')">  </i-button>
            <i-button type="ghost" @click.native="routeTo('/novel_chapter', novel.id)" style="margin-right: 10px">  </i-button>
          </Form-item>
        </i-form>
      </Card>
    </Row>
  </div>
</template>

<script>
  import { quillEditor } from 'vue-quill-editor'

  export default {
    data () {
      return {
        novel: {},
        formItem: {
          title: '',
          chinese_title: '',
          intro: '',
          order: 0,
          content: '',
          publish_status: 0
        },
        editorOption: {
          placeholder: '3000...',
          formula: true,
          modules: {
            toolbar: [
              ['bold', 'italic', 'underline', 'strike'],
              ['blockquote'],
              [{ 'indent': '-1' }, { 'indent': '+1' }],
              [{ 'size': ['small', false, 'large', 'huge'] }],
              [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
              [{ 'font': [] }],
              [{ 'align': [] }],
              ['clean']
            ]
          }
        },
        ruleFormItem: {
          title: [
            { required: true, message: '', trigger: 'blur' },
            { type: 'string', max: 100, message: '', trigger: 'blur' }
          ],
          chinese_title: [
            { required: true, message: '', trigger: 'blur' },
            { type: 'string', max: 50, message: '', trigger: 'blur' }
          ],
          intro: [
            { type: 'string', max: 500, message: '500', trigger: 'blur' }
          ],
          content: [
            { required: true, message: '', trigger: 'blur' }
          ],
          publish_status: [
            { required: true, message: '', trigger: 'change' }
          ]
        }
      }
    },
    created () {
      this.initList()
    },
    components: { quillEditor },
    computed: {
      editor () {
        return this.$refs.myTextEditor.quillEditor
      }
    },
    methods: {
      routeTo (s, novelId) {
        return this.$router.push({ path: s, query: { id: novelId } })
      },
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.formItem.novel_id = this.novel.id
            this.$http.post('admin_api/chapter/edit', this.formItem).then(response => {
              if (response.body.error === 0) {
                this.$Message.success(response.body.message)
                this.$router.push({ path: '/novel_chapter', query: { id: this.novel.id } })
              } else {
                this.$Notice.error({ title: '', desc: response.body.message })
              }
            })
          } else {
            this.$Message.error('?!')
          }
        })
      },
      onEditorChange ({ editor, html, text }) {
        this.formItem.content = html
      },
      initList () {
        if (!this.$route.query.id || !this.$route.query.novel_id) {
          this.$Notice.error({ title: '', desc: '~' })
          return this.$router.go(-1)
        }
        this.$http.post('admin_api/chapter/edit?get=one', { id: this.$route.query.id }).then(response => {
          if (response.body.error === 0) {
            this.formItem = response.body.data
            this.novel.id = this.$route.query.novel_id
            this.novel.title = response.body.data.novel_title
            this.novel.chinese_title = response.body.data.novel_chinese_title
          } else {
            this.$Notice.error({ title: '', desc: response.body.message })
          }
        })
      }
    }
  }
</script>

<style>
  .table-box .chapter-title {
    display: block;
    width: 100%;
    height: 32px;
    line-height: 32px;
    overflow: hidden;
  }
  .table-box .chapter-title span {
    font-size: 14px;
    color: #2baee9;
  }
  .editor-box {
    width: 100%;
    height: 300px;
  }
  .editor-box>div.ivu-form-item-content {
    line-height: 24px!important;
  }
</style>



